import { SHOW_RESULTS, RESET, SET_LENGTH } from "./types";

export default (state, action) => {
  switch (action.type) {
    case SHOW_RESULTS:
      console.log(state);
      console.log(action.payload);
      return { ...state, ...action.payload };
    case RESET:
      return {
        gallons: 0,
        weight: 0,
        lengths: {},
        length: 0,
      };
    case SET_LENGTH:
      return { ...state, length: state.lengths[action.payload] };
    default:
      return state;
  }
};
