import React, { useReducer } from "react";
import Context from "./Context";
import { SHOW_RESULTS, RESET, SET_LENGTH } from "./types";
import Reducer from "./Reducer";

const State = (props) => {
  const initialState = {
    gallons: 0,
    weight: 0,
    lengths: {},
    length: 0,
  };

  const [state, dispatch] = useReducer(Reducer, initialState);

  // Set Alert
  const showResults = (time, rate) => {
    const radii = [1, 1.25, 1.5, 2, 2.5, 3, 4, 5];
    // 231 in^3 == 1 gallon of water
    const volume = time * rate * 231;
    const gallons = (time * rate).toFixed(2);
    const weight = (gallons * 8.33).toFixed(2);
    let results = {};
    radii.map((e) => {
      results = Object.assign(
        {
          [e]: (volume / (Math.PI * Math.pow(0.5 * e, 2)) / 12).toFixed(2),
        },
        results
      );
    });
    dispatch({
      type: SHOW_RESULTS,
      payload: { weight, gallons, lengths: results, length: results["1"] },
    });
  };

  const setLength = (length) => {
    console.log(length);
    dispatch({ type: SET_LENGTH, payload: length });
  };

  const reset = () => {
    dispatch({ type: RESET });
  };

  return (
    <Context.Provider
      value={{
        gallons: state.gallons,
        weight: state.weight,
        lengths: state.lengths,
        length: state.length,
        showResults,
        reset,
        setLength,
      }}
    >
      {props.children}
    </Context.Provider>
  );
};

export default State;
