import React, { Fragment, useContext } from "react";
import { Nav, Card, Col, Row, Button } from "react-bootstrap";
import Context from "../context/Context";

const Result = () => {
  const context = useContext(Context);

  const { gallons, weight, length, reset, setLength } = context;

  const onClick = (e) => {
    setLength(e.target.id.substr(1));
  };

  return (
    <Fragment>
      <Card>
        <Card.Header>
          <Nav variant="tabs" defaultActiveKey="#s1">
            <Nav.Item>
              <Nav.Link eventKey="#s1" id="s1" onClick={onClick}>
                1"
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="#s1.25" id="s1.25" onClick={onClick}>
                1-1/4""
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="#s1.5" id="s1.5" onClick={onClick}>
                1-1/2"
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="#s2" id="s2" onClick={onClick}>
                2"
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="#s2.5" id="s2.5" onClick={onClick}>
                2.5"
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="#s3" id="s3" onClick={onClick}>
                3"
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="#s4" id="s4" onClick={onClick}>
                4"
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="#s5" id="s5" onClick={onClick}>
                5"
              </Nav.Link>
            </Nav.Item>
          </Nav>
        </Card.Header>
        <Card.Body>
          <Card.Text>
            <Row>
              <Col className="text-center">
                <h4>Length</h4>
              </Col>
            </Row>
            <Row>
              <Col className="text-center">{length} feet</Col>
            </Row>
          </Card.Text>
        </Card.Body>
        <Card.Footer>
          <Row>
            <Col md={6} className="text-right">
              Gallons:{" "}
            </Col>
            <Col md={6} className="text-left">
              {gallons}
            </Col>
          </Row>
          <Row>
            <Col md={6} className="text-right">
              Weight:{" "}
            </Col>
            <Col md={6} className="text-left">
              {weight}
            </Col>
          </Row>
        </Card.Footer>
      </Card>
      <Button className="mt-4" block="true" onClick={reset}>
        Reset
      </Button>
    </Fragment>
  );
};

export default Result;
